// src/pages/TranscriptionPage.jsx - Enhanced with Progress Simulation
import React, { useState, useEffect, useRef, useCallback } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import toast from 'react-hot-toast';

// Import components
import TranscriptionPageHeader from '../Components/transcription/TranscriptionPageHeader';
import ProcessingStatusSection from '../Components/transcription/ProcessingStatusSection';
import TranscriptPanel from '../Components/transcription/TranscriptPanel';

// Import hooks and context
import { useBackend } from '../contexts/BackendContext';
import { backendApi } from '../services/api';
import useAppStore from '../stores/appStore';

// Results validation function
const validateAndNormalizeResults = (fetchedData) => {
  if (!fetchedData) {
    console.log(' No data received');
    return null;
  }

  console.log(' Validating results structure:', {
    hasData: !!fetchedData,
    hasResults: !!fetchedData.results,
    hasSegments: !!fetchedData.results?.segments,
    segmentsCount: fetchedData.results?.segments?.length || 0,
    dataKeys: Object.keys(fetchedData),
    resultsKeys: fetchedData.results ? Object.keys(fetchedData.results) : []
  });

  if (fetchedData.results?.segments && Array.isArray(fetchedData.results.segments)) {
    console.log(' Valid results structure found');
    return fetchedData;
  }

  if (fetchedData.segments && Array.isArray(fetchedData.segments)) {
    console.log(' Found segments at root level, normalizing...');
    return {
      results: {
        segments: fetchedData.segments,
        metadata: fetchedData.metadata || {},
        speaker_stats: fetchedData.speaker_stats || {}
      }
    };
  }

  if (Array.isArray(fetchedData)) {
    console.log(' Found array data, treating as segments...');
    return {
      results: {
        segments: fetchedData,
        metadata: {},
        speaker_stats: {}
      }
    };
  }

  if (fetchedData.result?.segments) {
    console.log(' Found segments in result property, normalizing...');
    return {
      results: {
        segments: fetchedData.result.segments,
        metadata: fetchedData.result.metadata || {},
        speaker_stats: fetchedData.result.speaker_stats || {}
      }
    };
  }

  console.warn(' Could not find valid segments in data structure:', {
    hasResults: !!fetchedData.results,
    hasResultsSegments: !!fetchedData.results?.segments,
    hasSegments: !!fetchedData.segments,
    isArray: Array.isArray(fetchedData),
    hasResultSegments: !!fetchedData.result?.segments
  });
  
  return null;
};

const TranscriptionPage = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { isConnected, updateSession } = useBackend();
  
  const editorRef = useRef(null);
  const [currentView, setCurrentView] = useState('original');
  
  // Use Zustand store for persistent state
  const currentSessionId = useAppStore((state) => state.currentSessionId);
  const setCurrentSessionId = useAppStore((state) => state.setCurrentSessionId);
  const processingStatus = useAppStore((state) => state.processingStatus);
  const setProcessingStatus = useAppStore((state) => state.setProcessingStatus);
  const updateProgressSimulation = useAppStore((state) => state.updateProgressSimulation);
  const updateBackendProgress = useAppStore((state) => state.updateBackendProgress);
  const results = useAppStore((state) => state.results);
  const setResults = useAppStore((state) => state.setResults);
  const expandedTranscript = useAppStore((state) => state.expandedTranscript);
  const setExpandedTranscript = useAppStore((state) => state.setExpandedTranscript);
  const structures = useAppStore((state) => state.structures);
  const parameters = useAppStore((state) => state.parameters);

  const statusPollingRef = useRef(null);
  const progressSimulationRef = useRef(null);  //  NEW: Progress simulation interval
  const initializedRef = useRef(false);
  const hasShownCompletionToast = useRef(false);

  // Handle view changes from TranscriptPanel
  const handleViewChange = useCallback((view) => {
    setCurrentView(view);
  }, []);

  //  NEW: Progress simulation function
  const startProgressSimulation = useCallback(() => {
    if (progressSimulationRef.current) {
      clearInterval(progressSimulationRef.current);
    }

    const status = useAppStore.getState().processingStatus;
    if (!status?.simulationActive || !status?.startTime || !status?.estimatedDuration) {
      return;
    }

    console.log(' Starting progress simulation');

    progressSimulationRef.current = setInterval(() => {
      const currentStatus = useAppStore.getState().processingStatus;
      
      // Stop simulation if no longer active
      if (!currentStatus?.simulationActive) {
        clearInterval(progressSimulationRef.current);
        progressSimulationRef.current = null;
        return;
      }

      const now = Date.now();
      const elapsedTime = (now - currentStatus.startTime) / 1000; // seconds
      const progressRatio = Math.min(elapsedTime / currentStatus.estimatedDuration, 1);
      
      // Apply easing for smooth animation (ease-out curve)
      const easedProgress = 1 - Math.pow(1 - progressRatio, 2);
      
      // Scale to 5%-95% range
      const simulatedProgress = 5 + (easedProgress * 90);
      
      // Update simulated progress (this will be combined with backend progress in store)
      updateProgressSimulation(simulatedProgress);
      
      // Stop simulation when we reach the target
      if (simulatedProgress >= 95) {
        clearInterval(progressSimulationRef.current);
        progressSimulationRef.current = null;
        console.log(' Progress simulation reached 95%, stopping');
      }
    }, 100); // Update every 100ms for smooth animation
  }, [updateProgressSimulation]);

  //  NEW: Stop progress simulation
  const stopProgressSimulation = useCallback(() => {
    if (progressSimulationRef.current) {
      clearInterval(progressSimulationRef.current);
      progressSimulationRef.current = null;
      console.log(' Progress simulation stopped');
    }
  }, []);

  // Handle session initialization with refresh detection
  useEffect(() => {
    if (initializedRef.current) return;
    initializedRef.current = true;

    const stateData = location.state;
    
    // Check if we have results already (page refresh case)
    if (currentSessionId && results && !stateData) {
      console.log(' Page refresh detected - session already completed');
      if (processingStatus?.status !== 'completed') {
        setProcessingStatus({
          status: 'completed',
          progress: 100,
          message: 'Processing completed successfully!',
          simulationActive: false  //  NEW: Ensure simulation is stopped
        });
      }
      return;
    }

    // Handle new session from navigation
    if (stateData?.sessionId) {
      console.log(' Receiving new session:', stateData.sessionId);
      
      hasShownCompletionToast.current = false;
      setCurrentSessionId(stateData.sessionId);
      
      const currentStatus = useAppStore.getState().processingStatus;
      if (!currentStatus || currentStatus.status !== 'processing' || 
          currentStatus.sessionId !== stateData.sessionId) {
        setProcessingStatus({ 
          status: 'processing', 
          progress: 5,
          simulatedProgress: 5,       //  NEW: Initialize simulation state
          backendProgress: null,
          simulationActive: true,     //  NEW: Start simulation
          message: 'Starting audio processing...',
          fileInfo: stateData.fileInfo,
          sessionId: stateData.sessionId
        });
        
        //  NEW: Start progress simulation for new sessions
        setTimeout(() => {
          startProgressSimulation();
        }, 100);
      }
    }
  }, [location.state, setCurrentSessionId, setProcessingStatus, currentSessionId, results, processingStatus, startProgressSimulation]);

  // Start polling with better session validation and stop conditions
  useEffect(() => {
    const sessionToUse = currentSessionId;
    const hasValidResults = results && validateAndNormalizeResults(results);
    
    if (!sessionToUse || 
        (processingStatus?.status === 'completed' && hasValidResults) ||
        sessionToUse.startsWith('temp_') ||
        statusPollingRef.current) {
      
      if (processingStatus?.status === 'completed' && hasValidResults) {
        console.log(' Session already completed with valid results - skipping polling');
      }
      return;
    }

    console.log(' Starting status polling for session:', sessionToUse);
    startStatusPolling(sessionToUse);
  }, [currentSessionId, processingStatus?.status, results]);

  // Polling with comprehensive results validation
  const startStatusPolling = useCallback((sessionId) => {
    if (statusPollingRef.current) {
      clearInterval(statusPollingRef.current);
    }

    console.log(' Starting status polling for:', sessionId);
    let consecutiveErrors = 0;
    const MAX_ERRORS = 3;

    statusPollingRef.current = setInterval(async () => {
      try {
        const currentResults = useAppStore.getState().results;
        const currentStatus = useAppStore.getState().processingStatus;
        
        const hasValidResults = currentResults && validateAndNormalizeResults(currentResults);
        
        if (currentStatus?.status === 'completed' && hasValidResults) {
          console.log(' Stopping polling - session already completed with valid results');
          clearInterval(statusPollingRef.current);
          statusPollingRef.current = null;
          return;
        }

        const response = await backendApi.get(`/api/processing-status/${sessionId}`);
        const status = response.data;
        
        consecutiveErrors = 0;
        console.log(' Backend status:', status.status, status.progress || 'no progress');
        
        if (updateSession) {
          updateSession(sessionId, status);
        }

        if (status.status === 'completed') {
          console.log(' Backend processing completed');
          
          //  NEW: Stop progress simulation immediately
          stopProgressSimulation();
          
          clearInterval(statusPollingRef.current);
          statusPollingRef.current = null;
          
          const currentStatus = useAppStore.getState().processingStatus;
          const finalProgress = Math.max(100, currentStatus?.progress || 0);
          
          setProcessingStatus({
            status: 'completed',
            progress: finalProgress,
            message: 'Processing completed successfully!',
            simulationActive: false,  //  NEW: Stop simulation
            sessionId: sessionId
          });
          
          // Fetch results with comprehensive validation
          try {
            const resultsResponse = await backendApi.get(`/api/results/${sessionId}`);
            console.log(' Fetched results:', resultsResponse.data);
            
            const normalizedResults = validateAndNormalizeResults(resultsResponse.data);
            
            if (normalizedResults) {
              setResults(normalizedResults);
              console.log(' Results set successfully with normalized structure');
              
              if (!hasShownCompletionToast.current) {
                toast.success('Transcription completed!');
                hasShownCompletionToast.current = true;
              }
            } else {
              console.error(' Could not validate or normalize results structure');
              toast.error('Results received but data structure is invalid');
            }
          } catch (resultError) {
            console.error('Failed to fetch results:', resultError);
            toast.error('Processing completed but failed to fetch results');
          }
          
        } else if (status.status === 'failed') {
          console.log(' Backend processing failed');
          
          //  NEW: Stop progress simulation on failure
          stopProgressSimulation();
          
          clearInterval(statusPollingRef.current);
          statusPollingRef.current = null;
          setProcessingStatus({
            status: 'failed',
            message: status.message || 'Processing failed',
            progress: 0,
            simulationActive: false,  //  NEW: Stop simulation
            sessionId: sessionId
          });
          toast.error('Processing failed');
          
        } else if (status.status === 'processing') {
          //  ENHANCED: Update backend progress (store will handle combination with simulation)
          const currentStatus = useAppStore.getState().processingStatus;
          if (!currentStatus || status.progress > (currentStatus.backendProgress || 0)) {
            
            //  NEW: Use new backend progress update function
            updateBackendProgress(status.progress || 5);
            
            // Update message if provided
            if (status.message && status.message !== currentStatus?.message) {
              setProcessingStatus({
                ...useAppStore.getState().processingStatus,
                message: status.message,
                sessionId: sessionId
              });
            }
            
            console.log(' Updated backend progress:', status.progress);
          }
        }
        
      } catch (error) {
        consecutiveErrors++;
        console.error(`Status polling error (${consecutiveErrors}/${MAX_ERRORS}):`, error);
        
        if (error.response?.status === 404) {
          if (consecutiveErrors >= MAX_ERRORS) {
            console.log(' Session not found after multiple attempts, stopping polling');
            stopProgressSimulation();  //  NEW: Stop simulation on error
            clearInterval(statusPollingRef.current);
            statusPollingRef.current = null;
            setProcessingStatus({ 
              status: 'failed', 
              message: 'Session not found on server',
              progress: 0,
              simulationActive: false,  //  NEW: Stop simulation
              sessionId: sessionId
            });
            if (!sessionId.startsWith('temp_')) {
              toast.error('Session not found on server');
            }
          }
        } else if (consecutiveErrors >= MAX_ERRORS) {
          console.log(' Multiple network errors, stopping polling');
          stopProgressSimulation();  //  NEW: Stop simulation on error
          clearInterval(statusPollingRef.current);
          statusPollingRef.current = null;
        }
      }
    }, 3000); // Poll every 3 seconds
  }, [setProcessingStatus, setResults, updateSession, updateBackendProgress, stopProgressSimulation]);

  // Handle session reset
  const handleReset = useCallback(() => {
    if (statusPollingRef.current) {
      clearInterval(statusPollingRef.current);
      statusPollingRef.current = null;
    }
    
    //  NEW: Stop progress simulation on reset
    stopProgressSimulation();
    
    hasShownCompletionToast.current = false;
    setCurrentSessionId(null);
    setProcessingStatus(null);
    setResults(null);
    navigate('/');
  }, [navigate, setCurrentSessionId, setProcessingStatus, setResults, stopProgressSimulation]);

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      if (statusPollingRef.current) {
        console.log(' Cleaning up status polling on unmount');
        clearInterval(statusPollingRef.current);
        statusPollingRef.current = null;
      }
      
      //  NEW: Cleanup progress simulation on unmount
      if (progressSimulationRef.current) {
        console.log(' Cleaning up progress simulation on unmount');
        clearInterval(progressSimulationRef.current);
        progressSimulationRef.current = null;
      }
    };
  }, []);

  const hasSession = Boolean(currentSessionId);

  return (
    <div className="min-h-screen bg-gray-800 overflow-auto">
      <div className="p-6 max-w-7xl mx-auto">
        
        {/* Component 1: Header Section */}
        <TranscriptionPageHeader 
          currentSessionId={currentSessionId}
          results={results}
          structures={structures}
          parameters={parameters}
          hasSession={hasSession}
          editorRef={editorRef}
          currentView={currentView}
        />

        {/* Component 2: Processing Status and Stats */}
        <ProcessingStatusSection 
          hasSession={hasSession}
          processingStatus={processingStatus}
          onReset={handleReset}
          results={results}
          metadata={results?.results?.metadata}
          speakerStats={results?.results?.speaker_stats}
        />

        {/* Component 3: Centered Transcript Panel */}
        <div className="flex justify-center" style={{ height: 'calc(160vh - 480px)', minHeight: '960px' }}>
          <div 
            className="border-4 border-cyan-400 rounded-3xl p-2 w-full max-w-5xl overflow-hidden" 
            style={{ 
              maxWidth: 'calc(64rem + 100px)',
              height: '100%'
            }}
          >
            <TranscriptPanel 
              results={results}
              isExpanded={expandedTranscript}
              onToggleExpand={() => setExpandedTranscript(!expandedTranscript)}
              hasSession={hasSession}
              editorRef={editorRef}
              onViewChange={handleViewChange}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default TranscriptionPage;