// Comprehensive debug version to identify the exact issue
import React, { useState, useEffect, useRef } from 'react';
import { motion } from 'framer-motion';
import toast from 'react-hot-toast';
import { 
  BarChart3, 
  Loader2, 
  RefreshCw, 
  Brain, 
  Clock, 
  Search,
  Filter,
  X,
  AlertTriangle,
  CheckCircle,
  Lightbulb,
  Zap
} from 'lucide-react';

// Import components
import UnifiedAnalysisCard from '../Components/analysis/UnifiedAnalysisCard';
import { useBackend } from '../contexts/BackendContext';
import { backendApi } from '../services/api';
import useAppStore from '../stores/appStore';

const AnalysisPage = () => {
  // Local UI state only
  const [prompts, setPrompts] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [loadingPromptData, setLoadingPromptData] = useState(false);
  const [loadingPrompts, setLoadingPrompts] = useState(new Set());
  
  //  CRITICAL: Use Zustand store for persistent state
  const currentSessionId = useAppStore((state) => state.currentSessionId);
  const results = useAppStore((state) => state.results);
  const processingStatus = useAppStore((state) => state.processingStatus);
  const customPrompt = useAppStore((state) => state.customPrompt);
  const setCustomPrompt = useAppStore((state) => state.setCustomPrompt);
  
  //  MOST IMPORTANT: Analysis results from Zustand store
  const analysisResults = useAppStore((state) => state.analysisResults);
  const setAnalysisResults = useAppStore((state) => state.setAnalysisResults);
  const analysisProgress = useAppStore((state) => state.analysisProgress);
  const setAnalysisProgress = useAppStore((state) => state.setAnalysisProgress);
  
  //  SUPER DEBUG: Add real-time state monitoring
  const [debugInfo, setDebugInfo] = useState({});
  
  useEffect(() => {
    const interval = setInterval(() => {
      const currentStore = useAppStore.getState();
      setDebugInfo({
        timestamp: new Date().toISOString(),
        analysisResults: currentStore.analysisResults,
        analysisProgress: currentStore.analysisProgress,
        analysisResultsCount: Object.keys(currentStore.analysisResults || {}).length,
        analysisProgressCount: Object.keys(currentStore.analysisProgress || {}).length,
      });
    }, 1000);
    
    return () => clearInterval(interval);
  }, []);
  
  // Backend connection
  const { backendStatus, activeSessions } = useBackend();
  
  // Categories for filtering
  const categories = [
    { value: 'all', label: 'All Categories' },
    { value: 'general', label: 'General' },
    { value: 'meeting', label: 'Meeting' },
    { value: 'content', label: 'Content' },
    { value: 'analysis', label: 'Analysis' },
    { value: 'productivity', label: 'Productivity' }
  ];

  // Load prompts on mount
  useEffect(() => {
    loadPromptsFromDatabase();
  }, []);

  const loadPromptsFromDatabase = async () => {
    setLoadingPromptData(true);
    try {
      let promptsData = [];
      
      try {
        const response = await backendApi.get('/api/prompts/');
        if (response?.data?.prompts) {
          promptsData = Object.entries(response.data.prompts).map(([key, prompt]) => ({
            key,
            title: prompt.title || 'Untitled',
            description: prompt.description || '',
            category: prompt.category || 'general',
            icon: prompt.icon || 'Brain',
            is_active: true,
            ...prompt
          }));
        }
      } catch (error) {
        console.log('Using fallback prompts');
        promptsData = [
          {
            key: 'summary',
            title: 'Conversation Summary', 
            description: 'Generate a comprehensive summary',
            category: 'general',
            icon: 'FileText',
            is_active: true
          },
          {
            key: 'action_items',
            title: 'Action Items',
            description: 'Extract action items and tasks', 
            category: 'meeting',
            icon: 'CheckSquare',
            is_active: true
          }
        ];
      }

      console.log(` Loaded ${promptsData.length} prompts`);
      setPrompts(promptsData);
      
    } catch (error) {
      console.error(' Failed to load prompts:', error);
    } finally {
      setLoadingPromptData(false);
    }
  };

  const getTranscriptFromResults = () => {
    if (!results?.results?.segments) {
      return null;
    }
    return results.results.segments
      .map(segment => `${segment.speaker}: ${segment.text}`)
      .join('\n');
  };

  //  SUPER DETAILED ANALYSIS FUNCTION WITH STEP-BY-STEP LOGGING
  const runAnalysis = async (promptKey) => {
    console.log(`\n ============ STARTING ANALYSIS FOR: ${promptKey} ============`);
    console.log(` STEP 1: Current state before analysis:`, {
      currentSessionId,
      analysisResults: JSON.stringify(analysisResults, null, 2),
      analysisProgress: JSON.stringify(analysisProgress, null, 2),
      analysisResultsKeys: Object.keys(analysisResults || {}),
      analysisProgressKeys: Object.keys(analysisProgress || {}),
    });
    
    if (!currentSessionId) {
      toast.error('No session selected. Please process audio first.');
      return;
    }

    try {
      // Get transcript
      let transcript = getTranscriptFromResults();
      
      if (!transcript) {
        console.log(` STEP 2: No transcript in results, fetching from API...`);
        const sessionResponse = await backendApi.getResults(currentSessionId);
        if (!sessionResponse?.data?.results?.segments) {
          toast.error('No transcript available for analysis.');
          return;
        }
        transcript = sessionResponse.data.results.segments
          .map(segment => `${segment.speaker}: ${segment.text}`)
          .join('\n');
      }

      console.log(` STEP 3: Transcript ready, length: ${transcript.length} characters`);

      // Update loading state
      setLoadingPrompts(prev => new Set(prev).add(promptKey));
      
      // Update progress to processing
      console.log(` STEP 4: Setting progress to processing for ${promptKey}...`);
      const newProgressBefore = {
        ...analysisProgress,
        [promptKey]: { 
          status: 'processing', 
          progress: 0,
          timestamp: new Date().toISOString()
        }
      };
      console.log(` STEP 4a: New progress object:`, newProgressBefore);
      
      setAnalysisProgress(newProgressBefore);
      
      // Verify progress was set
      setTimeout(() => {
        const currentState = useAppStore.getState();
        console.log(` STEP 4b: Progress after setState:`, currentState.analysisProgress);
      }, 100);

      // Find the prompt
      const prompt = prompts.find(p => p.key === promptKey);
      if (!prompt) {
        throw new Error('Prompt not found');
      }
      console.log(` STEP 5: Found prompt:`, prompt);

      // Make API call
      console.log(` STEP 6: Making API call to /api/llm-process...`);
      const apiPayload = {
        transcript,
        prompt_key: promptKey
      };
      console.log(` STEP 6a: API payload:`, apiPayload);
      
      const analysisResponse = await backendApi.post('/api/llm-process', apiPayload);

      console.log(` STEP 7: Analysis API response received:`, {
        status: analysisResponse.status,
        data: analysisResponse.data,
        hasResult: !!(analysisResponse.data?.result || analysisResponse.data?.response),
        resultLength: (analysisResponse.data?.result || analysisResponse.data?.response)?.length || 0
      });

      // Prepare result data
      const resultData = {
        response: analysisResponse.data.result || analysisResponse.data.response,
        result: analysisResponse.data.result || analysisResponse.data.response,
        model: analysisResponse.data.model,
        prompt_title: analysisResponse.data.prompt_title || prompt.title,
        prompt_key: promptKey,
        timestamp: new Date().toISOString(),
        processing_time: analysisResponse.data.processing_time,
        raw_api_response: analysisResponse.data // Store full response for debugging
      };

      console.log(` STEP 8: Prepared result data:`, {
        promptKey,
        hasResponse: !!resultData.response,
        hasResult: !!resultData.result,
        responseLength: resultData.response?.length || 0,
        resultData: JSON.stringify(resultData, null, 2)
      });

      // Update results in store
      console.log(` STEP 9: Updating analysisResults in store...`);
      const newResultsBefore = {
        ...analysisResults,
        [promptKey]: resultData
      };
      console.log(` STEP 9a: New results object:`, newResultsBefore);
      
      setAnalysisResults(newResultsBefore);

      // Update progress to completed
      console.log(` STEP 10: Setting progress to completed...`);
      const newProgressComplete = {
        ...analysisProgress,
        [promptKey]: { 
          status: 'completed', 
          progress: 100,
          timestamp: new Date().toISOString()
        }
      };
      console.log(` STEP 10a: Final progress object:`, newProgressComplete);
      
      setAnalysisProgress(newProgressComplete);

      // Verify final state
      setTimeout(() => {
        const finalState = useAppStore.getState();
        console.log(` STEP 11: FINAL VERIFICATION:`, {
          analysisResults: finalState.analysisResults,
          analysisProgress: finalState.analysisProgress,
          hasResultForKey: !!finalState.analysisResults[promptKey],
          hasProgressForKey: !!finalState.analysisProgress[promptKey],
          resultContent: finalState.analysisResults[promptKey]?.response?.substring(0, 100) + '...'
        });
      }, 200);

      console.log(` STEP 12: Analysis completed successfully!`);
      toast.success(`Analysis completed: ${prompt.title}`);

    } catch (error) {
      console.error(` ANALYSIS FAILED at some step:`, error);
      
      const errorMessage = error.response?.data?.detail || error.message || 'Analysis failed';
      
      setAnalysisProgress(prev => ({
        ...prev,
        [promptKey]: { 
          status: 'failed', 
          progress: 0,
          error: errorMessage,
          timestamp: new Date().toISOString()
        }
      }));

      toast.error(errorMessage);
    } finally {
      setLoadingPrompts(prev => {
        const newSet = new Set(prev);
        newSet.delete(promptKey);
        return newSet;
      });
      console.log(` ============ ANALYSIS PROCESS COMPLETE FOR: ${promptKey} ============\n`);
    }
  };

  const filteredPrompts = Array.isArray(prompts) ? prompts.filter(prompt => {
    if (!prompt || typeof prompt !== 'object') return false;
    const title = prompt.title || '';
    const description = prompt.description || '';
    if (typeof title !== 'string' || typeof description !== 'string') return false;
    
    try {
      const matchesSearch = title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           description.toLowerCase().includes(searchTerm.toLowerCase());
      const matchesCategory = selectedCategory === 'all' || prompt.category === selectedCategory;
      return matchesSearch && matchesCategory;
    } catch (error) {
      return false;
    }
  }) : [];

  const isValidSession = currentSessionId && 
    (processingStatus?.status === 'completed' || results?.results?.segments);

  return (
    <div className="min-h-screen bg-gray-900 text-white">
      <div className="container mx-auto px-6 py-8">


        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mb-8"
        >
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <BarChart3 className="w-8 h-8 text-cyan-400" />
              <div>
                <h1 className="text-3xl font-bold text-white">Analysis Dashboard</h1>
                <p className="text-gray-400 mt-1">AI-powered insights and analysis</p>
              </div>
            </div>
            
            <div className="flex items-center space-x-4">
              <button
                onClick={loadPromptsFromDatabase}
                disabled={loadingPromptData}
                className="flex items-center space-x-2 px-4 py-2 bg-gray-700 hover:bg-gray-600 rounded-lg transition-colors"
              >
                <RefreshCw className={`w-4 h-4 ${loadingPromptData ? 'animate-spin' : ''}`} />
                <span>Refresh</span>
              </button>
              
              <button
                onClick={() => {
                  console.log(' Clearing analysis history...');
                  setAnalysisResults({});
                  setAnalysisProgress({});
                  toast.success('Analysis history cleared');
                }}
                className="flex items-center space-x-2 px-4 py-2 bg-red-600 hover:bg-red-700 rounded-lg transition-colors"
              >
                <X className="w-4 h-4" />
                <span>Clear History</span>
              </button>
            </div>
          </div>
        </motion.div>

        {/* Session Status */}
        {!isValidSession && (
          <div className="mb-6 p-4 bg-amber-900 border border-amber-700 rounded-xl">
            <div className="flex items-center space-x-3">
              <AlertTriangle className="w-5 h-5 text-amber-400" />
              <div>
                <p className="text-amber-200 font-medium">No Active Session</p>
                <p className="text-amber-300 text-sm">
                  Please process audio first to enable analysis features.
                </p>
              </div>
            </div>
          </div>
        )}

        {/* Search and Filter Controls */}
        <div className="mb-6 bg-gray-800 rounded-xl border border-gray-700 p-4">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between space-y-4 md:space-y-0">
            <div className="flex items-center space-x-4">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
                <input
                  type="text"
                  placeholder="Search analysis prompts..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="pl-10 pr-4 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:ring-2 focus:ring-cyan-500 focus:border-transparent"
                />
              </div>
              
              <select
                value={selectedCategory}
                onChange={(e) => setSelectedCategory(e.target.value)}
                className="px-4 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:ring-2 focus:ring-cyan-500 focus:border-transparent"
              >
                {categories.map(category => (
                  <option key={category.value} value={category.value}>
                    {category.label}
                  </option>
                ))}
              </select>
            </div>
            
            <div className="text-sm text-gray-400">
              {filteredPrompts.length} of {prompts.length} prompts
            </div>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Predefined Analysis */}
          <div className="lg:col-span-2">
            <h2 className="text-xl font-semibold text-white mb-4">Predefined Analysis</h2>
            
            {loadingPromptData ? (
              <div className="text-center py-12">
                <Loader2 className="animate-spin w-8 h-8 text-cyan-500 mx-auto" />
                <p className="text-gray-400 mt-2">Loading prompts...</p>
              </div>
            ) : filteredPrompts.length === 0 ? (
              <div className="text-center py-12 bg-gray-800 rounded-xl border border-gray-700">
                <Brain className="w-12 h-12 text-gray-600 mx-auto mb-4" />
                <p className="text-gray-400 mb-2">No analysis prompts available</p>
              </div>
            ) : (
              <div className="grid gap-4">
                {filteredPrompts.map((prompt) => {
                  //  DETAILED CARD LOGGING
                  const cardAnalysisResult = analysisResults[prompt.key];
                  const cardAnalysisProgress = analysisProgress[prompt.key];
                  
                  console.log(` RENDERING CARD for ${prompt.key}:`, {
                    promptKey: prompt.key,
                    hasAnalysisResult: !!cardAnalysisResult,
                    hasAnalysisProgress: !!cardAnalysisProgress,
                    analysisResultContent: cardAnalysisResult?.response?.substring(0, 50) + '...' || 'No content',
                    analysisProgressStatus: cardAnalysisProgress?.status || 'No status',
                    fullAnalysisResult: cardAnalysisResult,
                    fullAnalysisProgress: cardAnalysisProgress,
                    allResultKeys: Object.keys(analysisResults || {}),
                    allProgressKeys: Object.keys(analysisProgress || {})
                  });
                  
                  return (
                    <div key={prompt.key} className="relative">
                      
                      <UnifiedAnalysisCard
                        prompt={prompt}
                        selectedSession={currentSessionId}
                        analysisResults={analysisResults}
                        analysisProgress={analysisProgress}
                        isLoading={loadingPrompts.has(prompt.key)}
                        onRunAnalysis={runAnalysis}
                        onDownload={(promptKey) => {
                          console.log(' Download requested for:', promptKey);
                          // Simple download for debugging
                          const result = analysisResults[promptKey];
                          if (result?.response || result?.result) {
                            const blob = new Blob([result.response || result.result], { type: 'text/plain' });
                            const url = URL.createObjectURL(blob);
                            const link = document.createElement('a');
                            link.href = url;
                            link.download = `analysis_${promptKey}.txt`;
                            link.click();
                            URL.revokeObjectURL(url);
                            toast.success('Analysis downloaded');
                          } else {
                            toast.error('No result to download');
                          }
                        }}
                        showUsageStats={true}
                        isAdminView={false}
                      />
                    </div>
                  );
                })}
              </div>
            )}
          </div>

          {/* Custom Analysis */}
          <div className="lg:col-span-1">
            <h2 className="text-xl font-semibold text-white mb-4">Custom Analysis</h2>
            
            <div className="bg-gray-800 rounded-xl border border-gray-700 p-6">
              <div className="flex items-center space-x-2 mb-4">
                <Lightbulb className="w-5 h-5 text-yellow-400" />
                <h3 className="font-medium text-white">Create Custom Prompt</h3>
              </div>
              
              <textarea
                value={customPrompt}
                onChange={(e) => setCustomPrompt(e.target.value)}
                placeholder="Enter your custom analysis prompt here."
                className="w-full h-32 px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 resize-none focus:ring-2 focus:ring-cyan-500 focus:border-transparent"
              />
              
              <button
                onClick={() => runAnalysis('custom_analysis')}
                disabled={!isValidSession || !customPrompt.trim() || loadingPrompts.has('custom_analysis')}
                className={`w-full mt-4 py-2 rounded-lg font-semibold transition-colors flex items-center justify-center space-x-2 ${
                  isValidSession && customPrompt.trim() && !loadingPrompts.has('custom_analysis')
                    ? 'bg-cyan-500 text-white hover:bg-cyan-600'
                    : 'bg-gray-600 text-gray-400 cursor-not-allowed'
                }`}
              >
                {loadingPrompts.has('custom_analysis') ? (
                  <>
                    <Loader2 className="w-4 h-4 animate-spin" />
                    <span>Analyzing...</span>
                  </>
                ) : (
                  <>
                    <Zap className="w-4 h-4" />
                    <span>Run Analysis</span>
                  </>
                )}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AnalysisPage;