// src/Components/transcription/ProcessingStatusSection.jsx - Enhanced with Combined Progress
import React from 'react';
import { motion } from 'framer-motion';
import { 
  Loader2, 
  CheckCircle, 
  AlertCircle, 
  X,
  Clock,
  Users,
  FileAudio,
  MessageSquare
} from 'lucide-react';

// Processing Status Component
const ProcessingStatus = ({ status, onReset }) => {
  //  ENHANCED: Use the combined progress from store (simulation + backend)
  const displayProgress = status?.progress || 5;
  
  const getStatusConfig = () => {
    switch (status?.status) {
      case 'processing':
        return {
          icon: Loader2,
          iconProps: { className: "w-5 h-5 text-blue-500 animate-spin" },
          bgColor: 'bg-blue-50',
          borderColor: 'border-blue-200',
          textColor: 'text-blue-800'
        };
      case 'completed':
        return {
          icon: CheckCircle,
          iconProps: { className: "w-5 h-5 text-green-500" },
          bgColor: 'bg-green-50',
          borderColor: 'border-green-200',
          textColor: 'text-green-800'
        };
      case 'failed':
        return {
          icon: AlertCircle,
          iconProps: { className: "w-5 h-5 text-red-500" },
          bgColor: 'bg-red-50',
          borderColor: 'border-red-200',
          textColor: 'text-red-800'
        };
      default:
        return {
          icon: Loader2,
          iconProps: { className: "w-5 h-5 text-gray-500" },
          bgColor: 'bg-gray-50',
          borderColor: 'border-gray-200',
          textColor: 'text-gray-800'
        };
    }
  };

  const config = getStatusConfig();
  const IconComponent = config.icon;

  return (
    <motion.div
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      className={`${config.bgColor} ${config.borderColor} border rounded-xl p-4 mb-6`}
    >
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-3">
          <IconComponent {...config.iconProps} />
          <div>
            <h3 className={`font-medium ${config.textColor}`}>
              {status?.status === 'processing' && 'Processing Audio'}
              {status?.status === 'completed' && 'Processing Complete'}
              {status?.status === 'failed' && 'Processing Failed'}
              {!status?.status && 'Processing Status'}
            </h3>
            <p className="text-sm text-gray-600 mt-1">
              {status?.status === 'processing' && (status?.message || 'Processing your audio file...')}
              {status?.status === 'completed' && 'Your transcription is ready!'}
              {status?.status === 'failed' && (status?.message || 'Something went wrong during processing.')}
            </p>
          </div>
        </div>
        
        {/* Reset button for failed status */}
        {status?.status === 'failed' && (
          <button
            onClick={onReset}
            className="p-2 hover:bg-gray-200 rounded-lg transition-colors"
            title="Reset and try again"
          >
            <X className="w-5 h-5 text-gray-600" />
          </button>
        )}
      </div>

      {/* Progress bar - only show for processing status */}
      {(!status || status?.status === 'processing') && (
        <div className="space-y-2 mt-4">
          <div className="flex justify-between text-sm">
            <span className="text-gray-600">Progress</span>
            <span className="font-medium text-gray-900">{Math.round(displayProgress)}%</span>
          </div>
          <div className="w-full bg-gray-200 rounded-full h-3 overflow-hidden">
            <div 
              className="bg-gradient-to-r from-cyan-400 to-cyan-500 h-3 rounded-full transition-all duration-500 ease-out relative"
              style={{ width: `${Math.max(5, Math.min(100, displayProgress))}%` }}
            >
              {/*  ENHANCED: Show animation only when simulation is active */}
              {status?.simulationActive && (
                <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/30 to-transparent animate-pulse"></div>
              )}
            </div>
          </div>
          
          <div className="flex justify-between text-xs text-gray-500 mt-2">
            <span className={displayProgress >= 10 ? 'text-cyan-600 font-medium' : ''}>Upload</span>
            <span className={displayProgress >= 30 ? 'text-cyan-600 font-medium' : ''}>Analysis</span>
            <span className={displayProgress >= 60 ? 'text-cyan-600 font-medium' : ''}>Diarization</span>
            <span className={displayProgress >= 90 ? 'text-cyan-600 font-medium' : ''}>Complete</span>
          </div>
        </div>
      )}
    </motion.div>
  );
};

// Stats Summary Component
const StatsSummary = ({ metadata, speakerStats, hasData }) => {
  const formatTime = (seconds) => {
    if (!seconds) return '--:--';
    const mins = Math.floor(seconds / 60);
    const secs = Math.floor(seconds % 60);
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  const stats = [
    {
      icon: Clock,
      label: 'Duration',
      value: hasData ? formatTime(metadata?.total_duration) : '--:--',
      color: 'bg-blue-100 text-blue-800',
      isLoading: hasData && !metadata?.total_duration
    },
    {
      icon: Users,
      label: 'Speakers',
      value: hasData ? (metadata?.num_speakers || '--') : '--',
      color: 'bg-green-100 text-green-800',
      isLoading: hasData && !metadata?.num_speakers
    },
    {
      icon: FileAudio,
      label: 'Segments',
      value: hasData ? (metadata?.segments_count || '--') : '--',
      color: 'bg-purple-100 text-purple-800',
      isLoading: hasData && !metadata?.segments_count
    },
    {
      icon: MessageSquare,
      label: 'Language',
      value: hasData ? (metadata?.language || '--') : '--',
      color: 'bg-orange-100 text-orange-800',
      isLoading: hasData && !metadata?.language
    }
  ];

  return (
    <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
      {stats.map((stat, index) => {
        const IconComponent = stat.icon;
        return (
          <motion.div
            key={stat.label}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: index * 0.1 }}
            className="bg-white rounded-xl border border-gray-200 p-4 relative overflow-hidden"
          >
            <div className="flex items-center space-x-3">
              <div className={`p-2 rounded-lg ${stat.color}`}>
                <IconComponent className="w-4 h-4" />
              </div>
              <div className="flex-1 min-w-0">
                {stat.isLoading ? (
                  <div className="flex items-center justify-center space-x-1">
                    <Loader2 className="w-4 h-4 animate-spin" />
                    <span className="text-lg font-bold">--</span>
                  </div>
                ) : (
                  <div className="text-lg font-bold">{stat.value}</div>
                )}
              </div>
              <div className="text-sm opacity-80">{stat.label}</div>
              
              {stat.isLoading && (
                <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent animate-pulse"></div>
              )}
            </div>
          </motion.div>
        );
      })}
    </div>
  );
};

// Main ProcessingStatusSection Component
const ProcessingStatusSection = ({ 
  hasSession, 
  processingStatus, 
  onReset, 
  results, 
  metadata, 
  speakerStats 
}) => {
  // Only show processing status when actively processing or failed
  // Hide it when completed AND we have results to prevent UI clutter
  const shouldShowProcessingStatus = hasSession && 
    processingStatus && 
    (processingStatus.status === 'processing' || 
     processingStatus.status === 'failed' ||
     (processingStatus.status === 'completed' && !results?.results?.segments));

  return (
    <div>
      {/* Show processing status only when needed */}
      {shouldShowProcessingStatus && (
        <ProcessingStatus 
          status={processingStatus}
          onReset={onReset}
        />
      )}

      {/* Stats Summary - Always show when we have session data */}
      <StatsSummary 
        metadata={metadata || {}} 
        speakerStats={speakerStats || {}}
        hasData={hasSession}
      />
    </div>
  );
};

export default ProcessingStatusSection;